.fg {
  position: relative;
  margin: 0;
  input,
  textarea{
    border: none;
    border-bottom: 1px solid #C4C5CB;
    height: 4.5625em;
    width: 100%;
    font-size: 1em;
    line-height: 125%;
    background-color: transparent;
    color: #000;
    padding: 2.3em .62em 1em;
    &::placeholder {
      color: #B0B8BD;
    }
    &:focus {
      border-color: $primary;
    }
  }
  textarea{
    resize: none;
    overflow: auto;
    padding-bottom: .75em;
  }
  label{
    position: absolute;
    top: 1em;
    padding: 0 .62em;
    color: #697272;
    span{
      font-size: .75em;
      line-height: 150%;
    }
  }
}

.check {
  position: relative;
  input {
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
    &:checked+label {
      &::before {
        // background-color: $green;
        border: 3px solid #fff;
      }
    }
    &:disabled+label {
      color: #AAAAAA;
      pointer-events: none;
    }
  }
  label {
    padding-left: 40px;
    position: relative;
    min-height: 20px;
    font-size: 14px;
    line-height: 20px;
    cursor: pointer;
    &:before {
      content: '';
      position: absolute;
      z-index: 1;
      border-radius: 2px;
      background: rgba(255, 255, 255, 0.01);
      border: 1px solid rgba(255, 255, 255, 0.45);
      width: 20px;
      height: 20px;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
      transition: all .100s;
    }
  }
}
