.footer{
  background: #192028;
  padding: 1.2em 0;
  &__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__c{
    color: rgba(#fff,.7);
    line-height: 207%;
  }
  a.footer__c{
    &:hover{
      color: #fff;
    }
  }
  &__by{
    display: inline-flex;
    align-items: center;
    margin-top: 1em;
    color: #fff;
    svg{
      width: 2.5em;
      height: auto;
      margin-right: 1em;
    }
    span{
      font-size: .75em;
      line-height: 150%;
    }
  }
}


@media screen and (max-width: 576px){
  .footer{
    &__inner{
      flex-direction: column;
      text-align: center;
    }
    &__c{
      line-height: 130%;
      @include lastM(0 0 1em);
    }
    &__by{
      margin-top: 1em;
      order: 3;
      text-align: left;
      svg{
        width: 2em;
      }
      span{
        font-size: .75em;
      }
    }
  }
}
