.text47{
  font-size: 2.9375em;
  line-height: 146%;
}

.text45{
  font-size: 2.8125em;
  line-height: 125%;
}

.text26{
  font-size: 1.625em;
  line-height: 127%;
}

.text24{
  font-size: 1.5em;
  line-height: 154%;
}

.text23{
  font-size: 1.4375em;
  line-height: 152%;
}

.text22{
  font-size: 1.375em;
  line-height: 123%;
}

.text21{
  font-size: 1.3125em;
  line-height: 138%;
}

.text20{
  font-size: 1.25em;
  line-height: 140%;
}

.text19{
  font-size: 1.1875em;
  line-height: 120%;
}

.text18{
  font-size: 1.125em;
  line-height: 161%;
}

.text16{
  font-size: 1em;
  line-height: 193%;
}

.text15{
  font-size: .9375em;
  line-height: 127%;
}

.text14{
  font-size: .875em;
  line-height: 135%;
}

.text13{
  font-size: .8125em;
  line-height: 147%;
}

.font2{
  font-family: $font2;
}

.extra-bold-text{
  font-weight: 800;
}

.semi-bold-text{
  font-weight: 600;
}

.bold-text{
  font-weight: 700;
}

.primary-text{
  color: $primary;
}

.secondary-text{
  color: $secondary;
}

.dark-text{
  color: $dark;
}

.gray-text{
  color: $gray;
}

.uppercase-text{
  text-transform: uppercase;
}


@media screen and (max-width: 992px){
  .text47,
  .text45{
    font-size: 1.75em;
  }

  .text26{
    font-size: 1.25em;
  }

  .text23,
  .text18{
    font-size: 1em;
  }

  .text15,
  .text14{
    font-size: .75em;
  }
}


@media screen and (max-width: 576px){
  .text47,
  .text45{
    font-size: 1.5em;
  }

  .text26{
    font-size: 1.125em;
  }

  .text23{
    font-size: .875em;
  }
}
