.contacts-page{
  padding: 9.56em 0 7.75em;
  position: relative;
  overflow: hidden;
  background: rgba(228, 250, 253, 0.75);
  color: #fff;
  &__bg{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: auto;
    pointer-events: none;
    min-width: 1500px;
    &._view2{
      display: none;
    }
  }
  &__breadcrumbs{
    margin-top: -4.17em;
    position: relative;
    z-index: 2;
  }
  &__info{
    @include grid100gap(.75em);
    .main__text{
      max-width: 53em;
      position: relative;
      z-index: 2;
    }
  }
  .text47{
    position: relative;
    z-index: 2;
  }
}

.contacts2{
  padding: 2.25em 0 2.9em;
  &__grid{
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 1em));
    gap: 1.5em;
  }
  &__items{
    margin-top: 1em;
  }
  .contacts-item{
    max-width: unset;
    &._center{
      align-items: center;
    }
    ._pt63{
      padding-top: .63em;
    }
    &__icon{
      width: 2.6875em;
      height: 2.6875em;
      margin-right: .81em;
      img{
        max-width: 1em;
      }
    }
    &__link,
    &__address{
      font-size: 1.0625em;
      line-height: 159%;
      max-width: 11.4em;
      font-weight: 600;
    }
    &__mail{
      text-decoration: underline;
      color: $primary;
    }
    &__hint{
      color: #5A5B61;
      line-height: 193%;
    }
    &__whatsapp{
      display: flex;
      align-items: center;
      margin-top: .75em;
      color: #055E6A;
      font-weight: 600;
      &:hover{
        span{
          text-decoration-color: transparent;
        }
      }
      img{
        width: 2.6875em;
        display: block;
        flex-shrink: 0;
        margin-right: .875em;
      }
      span{
        font-size: 0.875em;
        text-decoration: underline;
        text-decoration-color: rgba(55,62,207,.3);
      }
    }
  }
}

.contact-socials{
  margin-top: 4em;
  .text20{
    text-align: center;
  }
  &__wrap{
    margin-top: 2em;
    display: flex;
    align-items: center;
    justify-content: space-between;
    &:before,
    &:after{
      content: '';
      display: block;
      width: 1em;
      height: .0625em;
      flex-grow: 1;
      background: #E3E8E8;
    }
  }
  .social{
    margin: 0 .75em;
    flex-shrink: 0;
    &__item{
      width: 4em;
      margin: 0 .47em;
    }
  }
}

.consult2{
  padding: 2.6em 0 5.375em;
}

.consult-form{
  margin: 2.44em auto 0;
  display: grid;
  grid-template-columns: repeat(2,calc(50% - .95em));
  gap: 1.75em 1.9em;
  width: 42.75em;
  max-width: 100%;
  ._full{
    grid-column: 1/-1;
  }
  &__nav{
    display: flex;
    justify-content: center;
    margin-top: .65em;
  }
  &__btn{
    width: 13.75em;
  }
}


@media screen and (max-width: 992px){
  .contacts-page{
    padding-top: 8em;
    padding-bottom: 5em;
    text-align: center;
    &__breadcrumbs{
      margin-top: -3.75em;
    }
    &__bg{
      &._view1{
        display: none;
      }
      &._view2{
        display: block;
      }
    }
    .main__text:before{
      display: none;
    }
  }

  .contacts2{
    padding: 1.5em 0;
    &__grid{
      grid-template-columns: repeat(2,calc(50% - 1em));
      gap: 2em;
    }
  }

  .consult2{
    padding: 1.5em 0 3em;
  }
}


@media screen and (max-width: 576px){
  .contacts2{
    &__grid{
      grid-template-columns: 100%;
    }
  }

  .contact-socials{
    padding: 0 .625em;
    margin-top: 2em;
    .text20{
      text-align: left;
    }
    &__wrap{
      margin-top: 1em;
      &:before,
      &:after{
        display: none;
      }
    }
    .social{
      margin: 0;
      justify-content: unset;
      &__item{
        width: 3em;
        &:first-child{
          margin-left: 0;
        }
      }
    }
  }

  .consult-form{
    grid-template-columns: 100%;
    gap: .75em;
  }
}
