@import "../vars.scss";
.breadcrumbs {
    border-radius: 0.9375em 0.9375em 0 0;
    background: #FFF;
    padding-top: 1.75em;
    padding-bottom: 1.75em;
    @media screen and (min-width: 1441px){
        margin: 0 auto;
        max-width: 1316px;
        width: 100%;
        .container{
            padding: 0 3em;

        }
    }

    &__item {
        font-size: .9375em;
        line-height: 187%;
        position: relative;
        font-weight: 400;
        display: inline;
        color: rgba($black,.5);
        &:last-child {
            color: $black;
        }
        &:not(:first-child) {
            &:before {
                content: '/';
                margin: 0 1.2em;
                flex-shrink: 0;
                color: rgba($black,.5);
            }
        }
    }
}


@media screen and (max-width: 992px){
    .breadcrumbs{
        padding-top: 1em;
        padding-bottom: 1em;
        &__item{
            &:not(:first-child){
                &:before{
                    margin: 0 .5em;
                }
            }
        }
    }
}
