@import 'vars';
@import 'components/_text.scss';
@import 'components/_buttons.scss';
@import "components/_inputs.scss";
.swiper{
  &-pagination{
    display: flex;
    align-items: center;
    justify-content: center;
    position: static;
    margin-top: 2.8em;
    &-bullet{
      border: 1px solid #373ECF;
      width: 0.875em;
      height: 0.875em;
      opacity: 1;
      background-color: #fff;
      margin: 0 .45em !important;
      position: relative;
      &:before{
        content: '';
        @include centerAbsolute;
        width: .375em;
        height: .375em;
        border-radius: 50%;
        background-color: $primary;
        opacity: 0;
        transition: inherit;
      }
      &-active{
        &:before{
          opacity: 1;
        }
      }
    }
  }
  &-button{
    width: 3.25em;
    height: 3.25em;
    border-radius: 50%;
    background-color: $primary;
    border: 1px solid $primary;
    &:hover{
      background-color: darken($primary,15%);
    }
    svg{
      width: .5em;
      height: auto;
    }
    &.swiper-button-disabled{
      opacity: 1;
      border-color: #E1E3E7;
      background-color: transparent;
      path{
        fill: #23272A;
      }
    }
  }
}



@media screen and (max-width: 992px){
  .swiper{
    &-pagination{
      margin-top: 1.25em;
    }
  }
}
