.blog{
  padding: 2.375em 0 6.25em;
  &__grid{
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 1.87em * 2 / 3));
    gap: 1.87em;
  }
  &__items{
    @include grid100gap(3.75em);
  }
  &__nav{
    margin-top: 6.25em;
    display: flex;
    justify-content: center;
  }
}

.blog-card{
  &__img{
    margin-bottom: 1.87em;
    img{
      width: 100%;
      display: block;
      border-radius: 1em;
    }
  }
  &__title{
    font-size: 1.125em;
    line-height: 121%;
    font-weight: 700;
    text-transform: uppercase;
    color: #05363D;
  }
  &__nav{
    display: flex;
    margin-top: .7em;
  }
}

.blog-item{
  padding-bottom: 4em;
}

.article{
  padding: 1em 0 2.4em;
  &__grid{
    display: grid;
    grid-template-columns: calc(100% - 6.5em) 3.25em;
    gap: 1.5em 3.25em;
  }
  h1,h3{
    font-weight: 700;
    font-family: $font2;
  }
  h1{
    font-size: 2.125em;
    line-height: 162%;
    max-width: 28.6em;
  }
  h3{
    font-size: 1.875em;
    line-height: 127%;
    margin-bottom: .5em;
  }
  p{
    font-size: 1.0625em;
    line-height: 182%;
  }
  p,
  blockquote{
    @include lastM(0 0 1em);
  }
  blockquote{
    border: .9375em solid #EEF5FC;
    padding: 3em 2.25em;
    font-weight: 600;
  }
  hr{
    display: block;
    height: 1.75em;
    border: none;
  }
  &__date{
    margin: .75em 0 1.75em;
  }
  &__img{
    width: 100%;
    display: block;
    border-radius: 1em;
    margin-bottom: 1em;
  }
  &__images{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 2em));
    gap: .5em 4em;
  }
}

.share{
  text-align: center;
  position: sticky;
  top: 1em;
  &__title{
    font-size: .75em;
    line-height: 125%;
    color: #9B9B9B;
    text-align: center;
    font-weight: 700;
    text-transform: uppercase;
  }
  &__link{
    margin-top: 1em;
    @include center;
    border: 1px solid #F5F5F5;
    border-radius: 50%;
    width: 3.25em;
    height: 3.25em;
    img{
      max-width: 1.25em;
      max-height: 1.375em;
    }
    &:hover{
      border-color: $primary;
    }
  }
}

.posts{
  padding: 2.4em 0 6.25em;
  overflow: hidden;
}

.posts-slider{
  margin-top: 2.38em;
  position: relative;
  .swiper{
    &-container{
      width: calc(100% + 1.75em);
      overflow: hidden;
    }
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      width: calc(100% / 3);
      height: auto;
      padding-right: 1.875em;
    }
    &-button{
      transform: translateY(-110%);
      &-next{
        right: -5em;
      }
      &-prev{
        left: -5em;
      }
    }
  }
  .blog-card{
    height: 100%;
    display: flex;
    flex-direction: column;
    &__img{
      @include imgCover(92.2%);
    }
    &__nav{
      margin-top: auto;
      padding-top: .7em;
    }
  }
}


@media screen and (max-width: 992px){
  .blog{
    padding: 1.5em 0 3em;
    &__grid{
      grid-template-columns: repeat(2,calc(50% - .5em));
      gap: 3.75em 1em;
    }
    &__nav{
      margin-top: 2em;
    }
  }

  .article{
    padding-bottom: 1.5em;
    &__grid{
      grid-template-columns: 100%;
    }
    h1{
      font-size: 1.5em;
    }
    h3{
      font-size: 1.25em;
    }
    p{
      font-size: .875em;
    }
    blockquote{
      border-width: 5px;
      padding: .75em;
    }
    hr{
      height: 1em;
    }
    &__img{
      border-radius: .5em;
    }
    &__images{
      grid-template-columns: repeat(2,calc(50% - .5em));
      column-gap: 1em;
    }
  }

  .share{
    display: flex;
    align-items: center;
    &__link{
      margin: 0 0 0 1em;
    }
  }

  .posts{
    padding: 1.5em 0 2em;
  }

  .posts-slider{
    margin-top: 1em;
    .swiper{
      &-container{
        overflow: unset;
        width: 100%;
      }
      &-slide{
        width: 20em;
        padding-right: 1em;
        max-width: 90%;
      }
      &-button{
        display: none;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .blog{
    &__grid{
      grid-template-columns: 100%;
      gap: 2em;
    }
    &__items{
      gap: 2em;
    }
  }

  .blog-card{
    &__title{
      font-size: 1em;
    }
  }

  .article{
    &__images{
      grid-template-columns: 100%;
    }
  }
}
