.main{
  background-image: url(../img/bg.svg);
  background-size: cover;
  background-position: right center;
  padding: 9.87em 0 5.38em;
  color: #fff;
  //border-radius: 1.5625em 1.5625em 0 0;
  &__inner{
    display: grid;
    grid-template-columns: 37.5em calc(100% - 51.625em) 14.125em;
    justify-content: space-between;
    gap: 2em 0;
  }
  &__text{
    margin-top: .5em;
    max-width: 27em;
    display: flex;
    &:before{
      content: '';
      display: block;
      flex-shrink: 0;
      margin-right: .75em;
      width: 2.3125rem;
      height: 0.0625rem;
      opacity: 0.3;
      background: #164B4E;
      transform: translateY(.94em);
    }
  }
  .main__text:before{
    display: none;
  }
  &__media{
    position: relative;
  }
  &__items{
    align-self: flex-end;
  }
}

.main-item{
  margin-top: 1.56em;
  padding: 2.19em 1.56em 2em;
  border-radius: 0.9375em;
  border: 1px solid rgba(255, 255, 255, 0.50);
  background: rgba(255, 255, 255, 0.30);
  color: #fff;
  &__top{
    font-weight: 900;
    display: flex;
    align-items: center;
    &._start{
      align-items: flex-start;
      .main-item__measure{
        margin-top: .2em;
      }
    }
  }
  &__num{
    font-size: 2.5625em;
    line-height: 127%;
  }
  &__measure{
    font-size: 1.6875em;
    line-height: 130%;
    margin-left: .1em;
  }
  &__desc{
    font-weight: 600;
  }
}

.main-offer{
  position: relative;
  z-index: 3;
  margin-top: 1.5em;
  padding: .62em;
  border-radius: 1.3125em;
  border: 1px dashed rgba(255, 255, 255, 0.28);
  &__inner{
    padding: 1.5em 1.8em 3.87em;
    background: #FFF;
    border-radius: .9375em;
    position: relative;
  }
  &__badge{
    z-index: 3;
    position: absolute;
    width: 8.75em;
    display: block;
    top: -4em;
    right: -4.375em;
    filter: drop-shadow(19px 52px 22px rgba(#000,.01))
            drop-shadow(11px 29px 19px rgba(#000,.05))
            drop-shadow(5px 13px 14px rgba(#000,.09))
            drop-shadow(1px 3px 8px rgba(#000,.1));
  }
  &__info{
    text-align: center;
    @include grid100gap(.375em);
  }
  &__items{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 1.2em;
  }
  &__btn{
    position: absolute;
    bottom: -2.25em;
    left: 50%;
    transform: translateX(-50%);
  }
}

.offer-item{
  display: flex;
  align-items: center;
  &__icon{
    width: 3.625em;
    height: 3.625em;
    @include center;
    border: 1px solid rgba(55, 62, 207, 0.05);
    border-radius: 50%;
    flex-shrink: 0;
    margin-right: .75em;
    img{
      max-width: 1.5em;
      display: block;
    }
  }
}

.teeth{
  padding-left: .5em;
  position: absolute;
  width: 27.75em;
  left: -2.5em;
  top: -1.5em;
  &__img{
    width: 100%;
    display: block;
    position: relative;
    z-index: 2;
  }
  &__img2{
    width: 100%;
    display: block;
    //transform: translateY(-38%);
  }
}

.crowns{
  padding: 7.2em 0 2.2em;
  &__inner{
    position: relative;
  }
  &__img{
    width: 41em;
    margin: .13em auto 0;
    img{
      width: 100%;
      display: block;
    }
  }
  &__items{
    display: grid;
    grid-template-columns: 19.375em 19.375em;
    justify-content: space-between;
    position: absolute;
    top: 50%;
    left: 0;
    transform: translateY(-50%);
    width: 100%;
    gap: 4.2em;
  }
  .offer-item{

    .text15{
      line-height: 160%;
    }
    @media screen and (min-width: 993px){
      &__icon{
        width: 5rem;
        height: 5rem;
        margin-right: 1.3em;
        img{
          max-width: 2.4375rem;
        }
      }
    }
    @media screen and (min-width: 577px){
      &:nth-child(odd){
        text-align: right;
        .offer-item__icon{
          margin: 0 0 0 1.3em;
          order: 2;
        }
      }
    }
  }
}

.block-info{
  text-align: center;
  @include grid100gap(1em);
  &__desc{
    max-width: 26.9em;
    margin: 0 auto;
  }
}

.cases{
  padding: 4.44em 0 2em;
}

.cases-slider{
  margin-top: 3em;
  padding: 0 4.7em 0 2.7em;
  position: relative;
  .swiper{
    &-container{
      overflow: hidden;
      width: calc(100% + 1.64em);
      padding-left: 2em;
      padding-top: 1.25em;
    }
    &-slide{
      width: 50%;
      padding-right: 1.64em;
    }
    &-pagination{
      padding-left: 2em;
    }
    &-button{
      transform: translateY(-110%);
      &-next{
        right: -1.6em;
      }
      &-prev{
        left: -1.6em;
      }
    }
  }
}

.case-card{
  text-align: center;
  &__images{
    padding-bottom: 61.6%;
    position: relative;
    margin-bottom: 2.5em;
    .beforeAfter{
      position: absolute !important;
      width: 100%;
      height: 100%;
      overflow: unset !important;
      img{
        border-radius: .875em;
      }
    }
  }
}

.beforeAfter{
  &__btn{
    width: 2em;
    height: 2em;
    background-color: $primary !important;
    background-image: url(../img/arrows.svg);
    @include bgImgSize(1.25em);
    opacity: 1 !important;
    i{
      display: none !important;
    }
  }
}

.warranty{
  padding: 4.2em 0 4.44em;
  .block-info{
    max-width: 30em;
    margin-left: auto;
    margin-right: auto;
  }
  &__grid{
    margin-top: 6.19em;
    display: grid;
    grid-template-columns: repeat(4,calc(25% - 5.4em / 4));
    gap: 3em 1.8em;
  }
}

.warranty-card{
  position: relative;
  &__badge{
    position: absolute;
    transform: translateX(-50%);
    left: 50%;
    top: -2.8em;
    z-index: 2;
    filter: drop-shadow(19px 52px 22px rgba(0,0,0,.01)) drop-shadow(11px 29px 19px rgba(0,0,0,.05)) drop-shadow(5px 13px 14px rgba(0,0,0,.09)) drop-shadow(1px 3px 8px rgba(0,0,0,.1));
  }
  &__img{
    @include imgCover(167%);
    img{
      border-radius: .9375em;
    }
    &:before{
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: linear-gradient(180deg, rgba(255, 255, 255, 0.00) 0%, rgba(255, 255, 255, 0.88) 100%);
      z-index: 2;
      border-radius: .9375em;
    }
  }
  &__content{
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    z-index: 3;
    padding: 1.8em 2.2em;
  }
}

.sections-group2{
  background-image: url(../img/round.svg);
  background-position: right bottom 5em;
  background-repeat: no-repeat;
  background-size: 41.25em auto;
}

.about{
  background-size: 100% auto;
  background-position: left bottom;
  padding: 3.5em 0 4.25em;
  position: relative;
  .container{
    position: relative;
    z-index: 2;
  }
  &__doctor{
    position: absolute;
    bottom: 11.56em;
    left: 0;
    width: 40.625em;
    display: block;
  }
  &__grid{
    padding-left: 13em;
    margin-top: .7em;
    display: grid;
    grid-template-columns: calc(100% - 28.1875em) 24.1875em;
    gap: 4em;
  }
  &__content{
    padding-top: 1em;
    .text14{
      line-height: 193%;
    }
  }
  &__nav{
    display: flex;
    margin-top: 1em;
  }
  &__btn{
    img{
      width: 1em;
      margin-left: 1em;
      flex-shrink: 0;
    }
  }
}

.sections-group3{
  overflow: hidden;
  background-image: url(../img/round2.svg);
  background-position: left bottom 52em;
  background-repeat: no-repeat;
  background-size: 42.125em auto;
}

.contacts{
  padding: 1.375em 1.25em 1.8em;
  border-radius: .625em;
  border: 1px solid #EAEAEA;
  background: #FFF;
  box-shadow: 0px 32px 32px 0px rgba(0, 0, 0, 0.04), 0px 73px 44px 0px rgba(0, 0, 0, 0.03), 0px 130px 52px 0px rgba(0, 0, 0, 0.01), 0px 203px 57px 0px rgba(0, 0, 0, 0.00);
  &__title{
    text-align: center;
  }
  &__top{
    display: flex;
    justify-content: space-between;
    margin: 1em 0 1.5em;
  }
  &__whatsapp{
    max-width: 6.9em;
    display: flex;
    align-items: flex-start;
    color: $primary;
    span{
      text-decoration: underline;
      text-decoration-color: rgba($primary,.3);
    }
    img{
      filter: drop-shadow(0 1px 5px rgba(0,0,0,.25));
      width: 1.1875em;
      flex-shrink: 0;
      margin-right: .3125em;
    }
  }
}

.contacts-item{
  display: flex;
  max-width: 20em;
  &__icon{
    background: #EBECFB;
    width: 2em;
    height: 2em;
    flex-shrink: 0;
    border-radius: 50%;
    @include center;
    margin-right: .75em;
    img{
      max-width: .8125em;
    }
  }
  &__content{
    padding-top: .35em;
  }
}

.social{
  display: flex;
  justify-content: center;
  margin-top: 1.12em;
  &__item{
    margin: 0 .3125em;
    width: 2.56794em;
    display: block;
    img{
      width: 100%;
      display: block;
      transition: all .3s;
    }
    &:hover{
      img{
        transform: translateY(-30%);
      }
    }
  }
}

.reviews,
.gallery{
  margin-top: 3.5em;
  .swiper{
    &-container{
      width: calc(100% + 1.87em);
    }
    &-slide{
      width: calc(100% / 3);
      padding-right: 1.87em;
    }
  }
}

.gallery-card{
  &__img{
    display: block;
    filter: drop-shadow(0 170px 68px rgba(0,0,0,.01))
            drop-shadow(0 96px 58px rgba(0,0,0,.05))
            drop-shadow(0 43px 43px rgba(0,0,0,.09))
            drop-shadow(0 11px 23px rgba(0,0,0,.1))
    ;
    border-radius: .9375em;
    overflow: hidden;
    @include imgCover(57.9%);
    img{
      border-radius: .9375em;
    }
  }
}

.faq{
  padding: 2.4em 0 3.5em;
  &__items{
    @include grid100gap(1em);
    width: 63.25em;
    max-width: 100%;
    margin: 2.75em auto 0;
  }
  &__nav{
    display: flex;
    justify-content: center;
    margin-top: 2.7em;
  }
}

.qa-card{
  &._active{
    .qa-card{
      &__title{
        font-weight: 600;
      }
      &__show{
        border-color: $primary;
      }
      &__btn{
        &:after{
          opacity: 0;
        }
      }
    }
  }
  &__show{
    transition: all .3s;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: .5em .8em .5em 1.8em;
    border-radius: .625em;
    border: 1px solid #EAEAEA;
    background: #FFF;
    box-shadow: 0px 32px 32px 0px rgba(0, 0, 0, 0.04), 0px 73px 44px 0px rgba(0, 0, 0, 0.03), 0px 130px 52px 0px rgba(0, 0, 0, 0.01), 0px 203px 57px 0px rgba(0, 0, 0, 0.00);
    &:hover{
      cursor: pointer;
      border-color: $primary;
    }
  }
  &__btn{
    width: 2.75em;
    height: 2.75em;
    flex-shrink: 0;
    margin-left: 1em;
    background-color: rgba($primary,.1);
    border-radius: 50%;
    position: relative;
    transition: all .3s;
    &:before,
    &:after{
      content: '';
      @include centerAbsolute;
      background-color: $primary;
      transition: inherit;
    }
    &:before{
      width: 1em;
      height: .1em;
    }
    &:after{
      width: .1em;
      height: 1em;
    }
  }
  &__hidden{
    padding: 1.28em 2em 0;
    display: none;
  }
  &__text{
    max-width: 57.8125em;
    .text14{
      line-height: 200%;
    }
  }
}

.reviews{
  margin-top: 0;
  padding: 2.625em 0 2.4em;
}

.reviews-slider{
  margin-top: 3em;
  .swiper{
    &-slide{
      width: 32.5675em;
      &-next~.swiper-slide{
        opacity: .4;
      }
    }
  }
}

.review-card{
  padding: 1.94em 1.56em 2.25em;
  border-radius: .9375em;
  background: #FFF;
  box-shadow:  0 .75em 1.625em 0 rgba(0, 0, 0, 0.10), 0 3em 3em 0 rgba(0, 0, 0, 0.09), 0 6.625em 4em 0 rgba(0, 0, 0, 0.05), 0 11.8125em 4.6875em 0 rgba(0, 0, 0, 0.01), 0 18.4375em 5.1875em 0 rgba(0, 0, 0, 0.00);
  display: flex;
  &__img{
    width: 4.9375em;
    height: 4.9375em;
    flex-shrink: 0;
    margin-right: 1.37em;
    img{
      width: 100%;
      height: 100%;
      border-radius: 50%;
      display: block;
    }
  }
  &__date{
    color: #878C93;
  }
  &__info{
    margin-bottom: .4em;
  }
  &__resource{
    display: flex;
    align-items: center;
    margin-top: 1em;
    img{
      width: 1.75em;
      display: block;
      flex-shrink: 0;
      margin-right: .5em;
    }
    a{
      color: $primary;
      text-decoration: underline;
      font-weight: 600;
    }
  }
}

.consult{
  padding: 2em 0 2.625em;
  &__inner{
    overflow: hidden;
    padding: 2.8em 3.4em;
    //background-color: blue;
    border-radius: .9375em;
    position: relative;
    background: linear-gradient(48deg, #02BCD3 0%, #6FF 33.96%, #6FF 100%), #D9D9D9;
    display: flex;
    justify-content: space-between;
    &:before{
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-image: url(../img/consult-bg.svg);
      background-size: cover;
      background-position: center;
    }
  }
  &__info{
    @include grid100gap(1em);
    max-width: 35.6875em;
  }
  .text24{
    letter-spacing: 0.045rem;
  }
  &__nav{
    margin-left: 1em;
    flex-shrink: 0;
    display: flex;
  }
}

._linear-bg{
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 100%;
  height: auto;
  min-width: 1220px;
}

.get{
  padding: 2em 0 2.625em;
  &__wrap{
    position: relative;
  }
  &__inner{
    background: linear-gradient(48deg, #02BCD3 0%, #6FF 33.96%, #6FF 100%), #D9D9D9;
    overflow: hidden;
    padding: 7em 3.8em 3.44em;
    border-radius: .9375em;
    position: relative;
    &:before{
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-image: url(../img/get-bg.svg);
      background-size: cover;
      background-position: center;
      pointer-events: none;
    }
  }
  &__title{
    font-size: 1.9375em;
    font-weight: 800;
    font-family: $font2;
    letter-spacing: 0.05813rem;
    text-align: center;
  }
  &__items{
    margin-top: 3em;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__badge{
    position: absolute;
    top: -3.375em;
    left: 50%;
    transform: translateX(-50%);
    width: 8.75em;
    display: block;
    filter: drop-shadow(19px 52px 22px rgba(0,0,0,.01)) drop-shadow(11px 29px 19px rgba(0,0,0,.05)) drop-shadow(5px 13px 14px rgba(0,0,0,.09)) drop-shadow(1px 3px 8px rgba(0,0,0,.1));
  }
  .offer-item{
    &__icon{
      background-color: #fff;
    }
    @media screen and (min-width: 993px){
      &__icon{
        width: 4.5625em;
        height: 4.5625em;
        margin-right: 1em;
        img{
          max-width: 1.75em;
        }
      }
    }
  }
}

.solutions{
  padding: 15.56em 0 5.6em;
  margin-top: -11.125em;
  background-color: #FAFAFA;
  .block-info{
    max-width: 41.25em;
    margin: 0 auto;
  }
  &__grid{
    margin-top: 2.8em;
    display: grid;
    grid-template-columns: repeat(4,calc(25% - 5.4em / 4));
    gap: 1.8em;
  }
  &__nav{
    display: flex;
    justify-content: center;
    margin-top: 2.6em;
  }
}

.solution-card{
  border-radius: 1.25em;
  background: #FFF;
  text-align: center;
  padding: 0 1em 2em;
  color: #333940;
  &__img{
    width: 11.6875em;
    height: 7.5625em;
    margin: 0 auto 3.35em;
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
      border-radius: 0 0 5.625em 5.625em;
      display: block;
    }
  }
  &__title{
    font-size: 1.0625rem;
    line-height: 159%;
    font-weight: 700;
  }
}



@media screen and (max-width: 992px){
  .main{
    padding: 10.94em 0 1.5em;
    &__inner{
      grid-template-columns: 27em calc(100% - 28em);
      gap: 1em;
    }
    .text47{
      max-width: 14em;
    }
    &__text{
      padding: 0;
      margin-top: 1em;
      max-width: 20em;
    }
    &__items{
      grid-column: 1/-1;
    }
  }

  .main-item{
    margin-top: .94em;
  }

  .teeth{
    width: 100%;
    left: 0;
  }

  .main-offer{
    padding: 0;
    border: none;
    &__inner{
      padding: 1em;
    }
    &__btn{
      position: relative;
      transform: unset;
      margin: 1em auto 0;
      left: unset;
      bottom: unset;
    }
    &__badge{
      width: 5em;
      top: -2em;
      right: -2em;
    }
  }

  .offer-item{
    &__icon{
      width: 2em;
      height: 2em;
      margin-right: .25em;
      img{
        max-width: 1em;
      }
    }
  }

  .crowns{
    padding: 2em 0;
    &__img{
      width: 20em;
      max-width: 100%;
    }
    &__items{
      grid-template-columns: 14em 14em;
    }
    .offer-item{
      @media screen and (min-width: 577px){
        .text15{
          font-size: .625em;
        }
      }
    }
  }

  .cases{
    padding: 1.5em 0;
    overflow: hidden;
  }

  .cases-slider{
    margin-top: 1.5em;
    padding: 0;
    .swiper{
      &-container{
        overflow: unset;
        padding: 0;
      }
      &-button{
        display: none;
      }
      &-slide{
        width: 20em;
        padding-right: 1em;
      }
      &-pagination{
        padding-left: 0;
      }
    }
  }

  .warranty{
    padding: 1.5em 0;
    &__grid{
      margin-top: 4em;
      grid-template-columns: repeat(2,calc(50% - .9em));
    }
  }

  .sections-group2,
  .sections-group3{
    background-image: unset;
  }

  .about{
    padding: 1.5em 0 2.5em;
    &__doctor{
      display: none;
    }
    &__grid{
      padding: 0;
      display: grid;
      grid-template-columns: calc(100% - 24em) 23em;
      gap: 2em 1em;
    }
  }

  .gallery{
    .swiper{
      &-slide{
        width: 50%;
      }
    }
  }

  .faq{
    padding: 1.5em 0;
    &__items,
    &__nav{
      margin-top: 1em;
    }
  }

  .qa-card{
    &__show{
      padding: .75em;
    }
    &__hidden{
      padding: 1em 0 0;
    }
    &__btn{
      width: 2.125em;
      height: 2.125em;
    }
    &__text{
      .text14{
        line-height: 150%;
      }
    }
  }

  .reviews{
    padding: 1.5em 0;
  }

  .reviews-slider{
    margin-top: 1.5em;
  }

  .consult{
    padding: 1.5em 0;
    &__inner{
      padding: 1em 1.5em;
    }
  }

  .get{
    padding: 1.5em 0;
    &__inner{
      padding: 3em 1em 1em;
    }
    &__badge{
      width: 5.5em;
    }
    &__title{
      font-size: 1.5em;
      line-height: 120%;
    }
    &__items{
      margin-top: 2em;
      display: grid;
      grid-template-columns: repeat(2,calc(50% - 1em));
      gap: 1.5em 2em;
      br{
        display: none;
      }
    }
  }

  ._linear-bg{
    min-width: 2000px;
  }

  .solutions{
    padding: 2em 0;
    margin-top: 0;
    &__grid{
      margin-top: 1.5em;
