.header{
  color: #fff;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  padding: 1.75em 0;
  z-index: 20;
  &__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__left{
    display: flex;
    align-items: center;
  }
  &__right{
    display: flex;
    align-items: center;
  }
  &__logo{
    width: 17.125em;
    display: block;
    flex-shrink: 0;
    img{
      width: 100%;
      display: block;
    }
  }
  &__desc{
    max-width: 8em;
    display: flex;
    align-items: center;
    padding-left: 1em;
    border-left: 1px solid rgba(#05363D,.1);
    margin-left: 1em;
    min-height: 3.625em;
  }
  &__btn{
    height: 3.375em;
    .m-btn__inner{
      padding: 0 1.81em;
      text-transform: uppercase;
      span{
        font-size: .75em;
      }
    }
  }
  @media screen and (min-width: 993px){
    &._index-page{
      .menu{
        display: none;
      }
    }
  }
}

.header-contacts{
  margin-right: .81em;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  &__phone{
    display: flex;
    align-items: center;
    font-weight: 600;
    img{
      width: 1.6875rem;
      flex-shrink: 0;
      display: block;
      margin-right: .56em;
    }
    span{
      font-size: 1.3125em;
      line-height: 120%;
    }
  }
  &__whatsapp{
    display: flex;
    align-items: center;
    color: rgba(#B5EDFF,.9);
    &:hover{
      color: $black;
      span{
        border-color: transparent;
      }
    }
    img{
      width: 0.98956rem;
      flex-shrink: 0;
      margin-right: .4em;
    }
    span{
      transition: inherit;
      font-size: 0.8125em;
      line-height: 116%;
      border-bottom: 1px solid rgba(#A7E3F7,.3);
    }
  }
}

.nav{
  display: flex;
  align-items: center;
  &__link{
    font-weight: 600;
    position: relative;
    @include lastM(0 3.75em 0 0);
    &:before{
      content: '';
      position: absolute;
      left: 0;
      bottom: 0;
      width: 100%;
      height: 1px;
      background: #FFB244;
      opacity: 0;
      transition: inherit;
    }
    span{
      font-size: 1em;
      line-height: 218%;
    }
    &:hover,
    &._active{
      &:before{
        opacity: 1;
      }
    }
  }
}

.menu__top,
.burger{
  display: none;
}

@media screen and (max-width: 992px){
  .header{
    padding: 1em 0;
    &__inner{
      padding-bottom: 3.5em;
      position: relative;
    }
    &__logo{
      width: 10.4375em;
    }
    &__desc{
      display: none;
    }
    &__btn {
      height: 2.75em;
      position: absolute;
      left: 50%;
      bottom: 0;
      transform: translateX(-50%);
      .m-btn__inner{
        padding: 0 1.5em;
      }
    }
    &._open{
      .menu{
        transform: translateX(0);
      }
    }
  }

  .header-contacts{
    align-items: center;
    justify-content: flex-end;
    flex-direction: row;
    margin: 0;
    width: 1em;
    &__whatsapp,
    &__phone{
      margin-left: .37em;
      img,
      svg{
        width: 1.375em;
        height: auto;
        margin: 0;
      }
      span{
        display: none;
      }
    }
  }

  .burger{
    display: block;
    width: 1em;
  }

  .menu{
    transition: all .5s;
    transform: translateX(-150%);
    position: fixed;
    top: 0;
    left: 0;
    height: calc(100 * var(--vh));
    width: 100%;
    background-color: #fff;
    z-index: 100;
    &__top{
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 1em .625em 1.75em;
      &-right{
        display: flex;
        align-items: center;
      }
    }
    &__close{
      width: .75em;
      svg{
        width: 100%;
        height: auto;
      }
    }
    &__btn{
      text-transform: uppercase;
      height: 2.5em;
      margin-left: .375em;
      .m-btn__inner {
        padding: 0 .75em;
        span{
          font-size: 0.5625em;
        }
      }
    }
  }

  .nav{
    display: block;
    &__link{
      height: 4.0625em;
      border-bottom: 1px solid #E9ECEC;
      display: flex;
      align-items: center;
      padding: 0 1.875em;
      margin: 0 !important;
      color: $black;
      text-transform: uppercase;
      &:before{
        display: none;
      }
      &:first-child{
        border-top: 1px solid #E9ECEC;
      }
      &._active{
        color: #1A6F7B;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .header{
    &__logo{
      //width: 5em;
    }
    &__btn{
      width: 12em;
      //height: 2em;
      //.m-btn__inner{
      //  padding: 0 1em;
      //  text-transform: unset;
      //  span{
      //    font-size: .625em;
      //  }
      //}
    }
  }

  .header-contacts{
    margin-right: .5em;
    &__whatsapp,
    &__phone{
      margin-left: .5em;
      img{
        width: 1.25em;
      }
    }
  }
}
