.sections-group3._bg-bottom{
  background-position: left bottom 2.87em;
}

.how-work{
  padding: 2.4em 0 2.625em;
  &__grid{
    display: grid;
    grid-template-columns: repeat(5,calc(20% - 1.8em * 4 / 5));
    gap: 1.8em;
    margin-top: 4.3em;
  }
  &__text{
    margin: 2.69em auto 0;
    text-align: center;
    max-width: 37.625em;
  }
}

.how-card{
  padding: 5.1em 1em 1.8em;
  background: linear-gradient(25deg, #02BCD3 0%, #00FBFB 58.26%, #23F2F2 100%);
  border-radius: .9375em;
  position: relative;
  text-align: center;
  color: #fff;
  &:nth-child(even){
    background: linear-gradient(216deg, #FFD7EF 0%, #FF80CE 36.46%, #FF64C3 100%);
  }
  &__icon{
    position: absolute;
    top: 0;
    left: 50%;
    transform: translate(-50%,-50%);
    background-color: #fff;
    box-shadow: 0 .6875em 1.75em 0 rgba(0, 0, 0, 0.10);
    border-radius: 50%;
    width: 5em;
    height: 5em;
    @include center;
    img{
      height: 2.125em;
    }
  }
  &__info{
    @include grid100gap(1em);
  }
  &__num{
    font-size: 3.4375rem;
    font-weight: 700;
    letter-spacing: .0625em;
  }
  &__text{
    font-size: 1.0625em;
    line-height: 153%;
  }
}

.what{
  padding: 2.5em 0 2.4em;
  position: relative;
  &__content{
    max-width: 45.5625em;
    position: relative;
    z-index: 2;
  }
  &__img{
    position: absolute;
    top: 0;
    right: 0;
    max-width: 50%;
  }
  &__info{
    @include grid100gap(1.5em);
  }
  &__info2{
    margin-top: 2.8em;
    @include grid100gap(1.75em);
  }
  &__desc{
    margin-top: 1em;
  }
  &__nav{
    margin-top: 1.44em;
    display: flex;
  }
  &__btn{
    width: 14.8125em;
    max-width: 100%;
    .m-btn__inner{
      span{
        font-size: 1.25em;
      }
    }
  }
  &__quote{
    display: inline-flex;
    align-items: center;
    padding: 1.44em 2.6em;
    margin-top: 2.75em;
    border-radius: 56.25em;
    border: .4375em solid rgba(91, 236, 241, 0.20);
    position: relative;
    &:before{
      content: '';
      position: absolute;
      width: 3.5625em;
      height: 3.5625em;
      background-image: url(../img/quote.svg);
      @include bgImgSize(100%);
      left: -.31em;
      top: -1.25em;
    }
  }
}

.about-main{
  position: relative;
  overflow: hidden;
  &__top{
    color: #fff;
  }
  &__inner{
    padding: 10.5em 0 9.25em;
    background: rgba(228, 250, 253, 0.90);
    overflow: hidden;
    position: relative;
  }
  &__img{
    position: absolute;
    top: 0;
    right: 0;
    width: 100%;
    max-width: 61.25em;
    pointer-events: none;
    z-index: 3;
    &._mob{
      display: none;
    }
  }
  &__content{
    position: relative;
    z-index: 2;
    max-width: 32.125em;
  }
  &__desc{
    margin-top: .94em;
    padding-top: .98em;
    position: relative;
    color: #fff;
    &:before{
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 2.6875em;
      height: 0.25em;
      background: #00DDFF;
    }
  }
  .breadcrumbs{
    margin-top: -4.17em;
    position: relative;
    z-index: 2;
  }
  &__gradients{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: auto;
    pointer-events: none;
  }
}

.about2{
  padding: .7em 0 2.65em;
  position: relative;
  z-index: 4;
  &__info{
    max-width: 37.4375em;
  }
}


@media screen and (max-width: 992px){
  .how-work{
    padding: 1.5em 0;
    &__grid{
      grid-template-columns: repeat(3,calc(100% / 3 - 1.8em * 2 / 3));
      row-gap: 4em;
      margin-top: 3em;
    }
    &__text{
      margin-top: 1.5em;
    }
  }

  .what{
    padding: 1.5em 0;
    &__info{
      gap: .5em;
    }
    &__info2{
      margin-top: 1em;
      gap: .5em;
    }
    &__desc{
      .text20{
        font-size: 1em;
      }
    }
    &__quote{
      margin-top: 1.5em;
      padding: .5em 1.25em;
      &:before{
        width: 2em;
        height: 2em;
      }
    }
  }

  .about-main{
    &__inner{
      padding: 8em 0 3em;
      position: static;
      overflow: unset;
      background-color: transparent;
    }
    &__top{
      background-image: url(../img/bg2.svg);
      background-size: cover;
      background-position: center;
      position: relative;
      padding-bottom: 25.31em;
    }
    &__img{
      position: absolute;
      top: 100%;
      left: 0;
      width: 100%;
      transform: translateY(-27.75em);
    }
    &__gradients{
      height: 100%;
      width: auto;
      left: unset;
      right: -20%;
      transform: unset;
      display: none;
    }
    .text24{
      font-size: 1.125em;
    }
    .breadcrumbs{
      margin-top: -2em;
      z-index: 3;
    }
  }
}


@media screen and (max-width: 576px){
  .how-work{
    &__grid{
      grid-template-columns: repeat(2,calc(50% - .25em));
      gap: 2.5em .5em;
      margin-top: 2em;
    }
  }

  .how-card{
    padding: 3em .5em 1em;
    &__icon{
      width: 3.5em;
      height: 3.5em;
      img{
        height: 1.75em;
      }
    }
    &__info{
      gap: .5em;
    }
    &__num{
      font-size: 2.75em;
    }
    &__text{
      font-size: .875em;
    }
  }

  .what{
    &__img{
      display: none;
    }
  }

  .about-main{
    &__inner{
      text-align: center;
    }
    &__top{
      overflow: hidden;
    }
    &__img{
      top: unset;
      bottom: 0;
      transform: translateY(7em);
      &._desktop{
        display: none;
      }
      &._mob{
        display: block;
      }
    }
    &__desc{
      padding-left: .625em;
      padding-right: .625em;
      &:before{
        left: 50%;
        transform: translateX(-50%);
      }
      .text24{
        font-size: 1.0625em;
      }
    }
  }
}
