.modal {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: calc(100 * var(--vh));
    overflow-y: auto;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 80;
    opacity: 0;
    pointer-events: none;
    transition: all .3s;

    &__bg {
        background: rgba(#192028, 0.97);
        position: fixed;
        width: 100%;
        height: calc(100 * var(--vh));
        top: 0;
        left: 0;
        display: none;
        z-index: 2;
    }

    &--active {
        opacity: 1;
        pointer-events: unset;

        .modal__bg {
            display: block;
        }
    }

    &__dialog {
        background: #fff;
        width: 30.0625em;
        max-width: calc(100% - 1em);
        position: relative;
        z-index: 3;
        border-radius: 1.0625em;
    }

    &__scroll {
        overflow-y: auto;
        max-height: calc(100 * var(--vh));
        padding: 3em 3.5em 3.5em;
    }

    &__close {
        position: absolute;
        right: 1em;
        top: 1em;
        width: 1.125em;
        background-color: transparent;
        svg {
            width: 100%;
            height: auto;
        }
        &:hover{
            g{
                opacity: 1;
            }
        }
    }
    &__info{
        text-align: center;
        .text24{
            color: #05363D;
        }
        .text16{
            color: #666D6D;
        }
    }
    .consult-form{
        grid-template-columns: 100%;
        gap: 1.5em;
        width: 100%;
        margin-top: 1.44em;
        &__nav{
            margin: 0;
        }
        &__btn{
            width: 100%;
        }
    }
}


@media screen and (max-width: 992px){
    .modal{
        &__scroll{
            padding-left: 1em;
            padding-right: 1em;
        }
        &__info{
            .text24{
                font-size: 1.25em;
            }
            .text16{
                line-height: 140%;
                font-size: .875em;
            }
        }
        .consult-form{
            gap: 1em;
            margin-top: 0;
        }
    }
}
