@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    height: 4.375em;
    background-color: transparent;
    &:focus {
        box-shadow: none;
    }
    &__inner{
        padding: 0 3.25em;
        border-radius: inherit;
        width: 100%;
        height: 100%;
        @include center;
        position: relative;
        z-index: 2;
        transition: inherit;
        span{
            font-size: 1em;
        }
    }
}


.m-btn-pink{
    border-radius: 900px;
    position: relative;
    font-weight: 700;
    color: #fff;
    &:hover{
        .m-btn__inner{
            //background-size: 200% auto;
            box-shadow: 0 .6875em 1.5625em 0 rgba(0, 9, 20, 0.09), 0 -.0625em 0 0 rgba(255, 255, 255, 0.08) inset, 0 .6875em 2.5625em 0 rgba(0, 9, 20, 0.09);
        }
        &:before{
            box-shadow: 0 .75em 2.5em -.75em rgba(169, 15, 35, 0.9);
        }
    }
    .m-btn__inner{
        box-shadow: 0 .6875em 1.5625em 0 rgba(0, 9, 20, 0.09), 0 -.0625em 0 0 rgba(255, 255, 255, 0.08) inset, 0 .6875em 1.5625em 0 rgba(0, 9, 20, 0.09), 0 -.0625em 0 0 rgba(255, 255, 255, 0.13) inset;
        background: linear-gradient(119deg, #213262 0%, #FA04D5 100%), linear-gradient(180deg, #FF327C 0%, #FF3A44 100%), linear-gradient(18deg, #FD3444 0%, #FA795A 100%), linear-gradient(132deg, #FEB203 0%, #FB2B1D 100%), radial-gradient(99.03% 95.74% at 51.81% 60.00%, #1470DB 0%, #0E55A9 100%), radial-gradient(53.96% 81.43% at 49.80% 18.57%, #35E6D4 0%, #2BB6BB 100%), radial-gradient(66.27% 100% at 50.20% 0.00%, #66EEBA 0%, #2FDADF 100%);
    }
    &:before{
        content: '';
        position: absolute;
        width: 100%;
        height: calc(100% + .25em);
        border-radius: inherit;
        background: #821E95;
        top: 0;
        box-shadow: 0 .5625em 2.3125em -.9375em rgba(169, 15, 35, 0.77);
        transition: inherit;
    }
    &._blue{
        color: $black;
        .m-btn__inner{
            background: linear-gradient(180deg, #3BFCE5 0%, #5FE2FF 100%);
            box-shadow: none;
        }
        &:before{
            background: #269FB4;
            box-shadow: 0px 9px 37px -15px rgba(46, 34, 36, 0.77);
        }
        &:hover{
            color: $black;
            .m-btn__inner{
                background-size: 200% auto;
                box-shadow: 0px 9px 37px -15px rgba(46, 34, 36, 0.77);
            }
            &:before{
                background: darken(#269FB4,20%);
            }
        }
    }
}

.m-btn-blue{
    padding: 0 2.06em;
    height: 3.1875em;
    border-radius: 56.25em;
    background-color: $primary;
    color: #fff;
    font-weight: 600;
    text-transform: uppercase;
    span{
        font-size: 0.8125em;
        line-height: 120%;
    }
    &:hover{
        background: #456CF8;
        color: #fff;
    }
}

.m-btn-blue-outline{
    padding: 0 2.94em;
    height: 4.5em;
    border-radius: 56.25em;
    color: $primary;
    border: 1px solid $primary;
    font-weight: 700;
    text-transform: uppercase;
    span{
        font-size: 1em;
        line-height: 120%;
    }
    &:hover{
        background: $primary;
        color: #fff;
    }
}


@media screen and (max-width: 992px){
    .m-btn{
        height: 3.25em;
        &__inner{
            padding: 0 1.5em;
            span{
                font-size: .875em;
            }
        }
    }
}
